import React from 'react';
import Title from '../Title/Title';
import SkillsSection from '../skills/Skills';
import './about.scss';

function AboutPage() {
  return (
    <div className='AboutPage' id='about'>
      <Title title={'Skills'} />
      <div className='skillsContainer'>
        <SkillsSection
          skill={'Javascript'}
          progress={'70%'}
          width={'70%'}
          img={
            'https://upload.wikimedia.org/wikipedia/commons/thumb/9/99/Unofficial_JavaScript_logo_2.svg/1200px-Unofficial_JavaScript_logo_2.svg.png'
          }
        />
        <SkillsSection
          skill={'Typescript'}
          progress={'50%'}
          width={'50%'}
          img={
            'data:image/png;base64,'
          }
        />
        <SkillsSection
          skill={'React Js'}
          progress={'70%'}
          width={'70%'}
          img={
            'data:image/png;base64,'
          }
        />
        <SkillsSection
          skill={'Vue.js'}
          progress={'70%'}
          width={'70%'}
          img={
            'data:image/png;base64,'
          }
        />
        <SkillsSection
          skill={'Redux'}
          progress={'65%'}
          width={'65%'}
          img={
            'data:image/png;base64,'
          }
        />
        <SkillsSection
          skill={'Vuex'}
          progress={'60%'}
          width={'60%'}
          img={
            'https://user-images.githubusercontent.com/7110136/29002857-9e802f08-7ab4-11e7-9c31-604b5d0d0c19.png'
          }
        />
        <SkillsSection
          skill={'Next.js'}
          progress={'50%'}
          width={'50%'}
          img={
            'data:image/png;base64,'
          }
        />
        <SkillsSection
          skill={'Express.js'}
          progress={'65%'}
          width={'65%'}
          img={
            'https://www.pngfind.com/pngs/m/136-1363736_express-js-icon-png-transparent-png.png'
          }
        />
        <SkillsSection
          skill={'MongoDB'}
          progress={'70%'}
          width={'70%'}
          img={
            'data:image/png;base64,'
          }
        />
      </div>
    </div>
  );
}

export default AboutPage;
