import './menu.scss';

export default function Menu({ menuOpen, setMenuOpen, outClick }) {
  return (
    <div className={'menu ' + (menuOpen && 'active')} onClick={outClick}>
      <ul>
        <li
          onClick={() => {
            setMenuOpen(false);
          }}>
          <a href='#intro'>Home</a>
        </li>
        <li onClick={() => setMenuOpen(false)}>
          <a href='#about'>Skills</a>
        </li>
        <li onClick={() => setMenuOpen(false)}>
          <a href='#portfolio'>Portfolio</a>
        </li>
        <li onClick={() => setMenuOpen(false)}>
          <a href='#contact'>Contact</a>
        </li>
      </ul>
    </div>
  );
}
